<template>
    <div class="rText">
        <textarea id="demo" style="display: none;"></textarea>
        <button type="button" class="layui-btn" @click="headleClick">提交留言</button>
    </div>
</template>

<script>
    
    export default {
        name:'rText',
        data(){
            return{
                index:0,
                layedit:null,
            }
        },
        mounted(){
            layui.use('layedit', ()=>{
                this.layedit = layui.layedit;
                this.index = this.layedit.build('demo',
                    {tool:['face']}
                ); //建立编辑器
                
            });
        },
        methods:{
            headleClick(){
                //获取留言板上的信息
                this.$emit("bui",this.layedit.getContent(this.index));
                // console.log(this.layedit.getContent(this.index))

                setTimeout(()=>{ //刷新出我们的新留言
                    window.location.reload();
                },800)
            }
        }
    }
</script>
<style lang="less">

</style>
