<template>
    <div class="article">
        <headerNav></headerNav>
        <section class="article-main">
            <div class="article-con">
                <div class="art-talk">
                    <h1>{{artFullCon.title}}</h1>
                    <p>文章类型: <span>{{artFullCon.tag}}</span></p>
                </div>
                <div class="time">
                    <p>{{artFullCon.date | time}}</p>
                </div>
                <div class="art-content">
                    {{artFullCon.content}}
                </div>
                <p class="say">广而告之,不为钱财</p>
            </div>
        </section>
        <foter></foter>
    </div>
</template>

<script>
import headerNav from '@/components/HeaderNav.vue'
import foter from "../components/footer"

export default {
    name:"Article",
    data(){
        return{
            artFullCon:"",//文章信息数据
        }
    },
    components:{
        headerNav,
        foter
    },
    filters:{
        time(value){
            let val = new Date(value)
            return val.toLocaleString();
        }
    },
    methods:{
        getFullFile(){//文章信息获取
            let id = this.$route.params.aid
            this.$axios.post("/api/article/fullFile",{id})
                .then(res=>{
                    
                    this.artFullCon = res.data
                })
                .catch(err=>{
                    console.log(err)
                })
        }
    },
    mounted(){
        this.getFullFile();
    }


}
</script>

<style lang="less">
html{
    font-size: 100%;
}
.article{
    width: 100%;
    overflow: hidden;
    .article-main{
        margin: 80px auto;
        width: 100%;
        max-width: 1360px;
        padding: 20px;
        box-sizing: border-box;
        .article-con{
            width: 100%;
            min-height: 800px;
            background-color: #fff;
            padding: 40px;
            box-sizing: border-box;
            position: relative;
            .art-talk{
                // background-color: skyblue;
                border-bottom: 1px solid grey;
                line-height: 1.8rem;
                font-weight: bold;
                span{
                    color:coral;
                }
            }
            .time{
                position: absolute;
                top: 40px;
                right: 40px;
                p{
                    font-size: 1.5rem;
                    color: #6BC30D;
                }
            }
            .art-content{
                width: 100%;
                // background-color: red;
                padding: 20px 0;
                font-size: 1rem;
                border-bottom: 1px solid grey;
                margin-bottom: 50px;
                text-indent: 1rem;
            }
            .say{
                
                width: 100%;
                text-align: center;
                font-size: 1.6rem;
                font-weight: bold;
                
            }
        }
    }
}
</style>