import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Index from "../views/Index.vue"
import Diary from "../views/Diary.vue"
import Links from "../views/Links.vue"
import Message from "../views/Message.vue"
import Article from "../views/Article.vue"

// #6BC30D

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    redirect: "/index/0"
  },
  {
    path: '/about',
    name: 'about',
    component: () => import( '../views/About.vue')
  },
  {
    path:"/home",
    name:"Home",
    component: Home
  },
  {
    path:"/index/:aid",
    name:"index",
    component: Index
  },
  {
    path:"/diary",
    name:"diary",
    component: Diary
  },
  {
    path:"/links",
    name:"links",
    component: Links
  },
  {
    path:"/message",
    name:"message",
    component: Message
  },
  {
    path:"/article/:aid",
    name:"article",
    component: Article
  }

]

const router = new VueRouter({
  routes
})

export default router
