<template>
    <div class="login">
        <el-dialog 
            title="MasterVerify" 
            :visible.sync="dailog.show"
            :append-to-body = "true"
        >
            <el-form 
                :model="loginForm"  
                class="loginform"
                :rules="rules" 
                ref="loginform" 
                label-width="150px"
            >
                <el-form-item label="用户名" prop="name">
                <el-input v-model="loginForm.name" ></el-input>
                </el-form-item>
                <el-form-item label="邮箱" prop="email">
                <el-input v-model="loginForm.email" ></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                <el-input type="password" v-model="loginForm.password" ></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button @click="resetForm('loginform')">重 置</el-button>
                    <el-button type="primary" @click="submitForm('loginform')">登 入</el-button>
                </el-form-item>
            </el-form>
            
        </el-dialog>
    </div>
</template>

<script>
import jwt_decode from 'jwt-decode'; //引入插件解析token
  export default {
    name:"login",
    props:{
        dailog:Object, //灰常重要        
    },
    data() {
      return {
        loginForm: {
          name: "",
          email:"",
          password:"",
        },

        rules: {
          password: [
            {required:true,message:"密码不能为空",trigger:'blur'},
          ],
          email: [
            {required:true,type: 'email',message:'邮箱格式不正确',trigger:'blur'}
          ],
          name: [
            {required:true,message:"用户名不能为空",trigger:'blur'},
          ]
        }

      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$axios.post("/api/user/login",this.loginForm)
                .then(res=>{
                    this.$message({
                        message:"登录成功",
                        type:"success"
                    })
                    const {token} = res.data //解构赋值
                    window.sessionStorage.setItem("eleToken",token); //缓存
                    const decode = jwt_decode(token);  //解析数据
                    console.log(decode);
                    setTimeout(()=>{
                        window.location.reload();
                    },500);
                })
                .catch(err=>{
                    this.$message({
                        message:"登入出现问题",
                        type:"warning"
                    });
                })

               
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }

  };
</script>

<style lang="less">
    
    
    .el-dialog__header{
        margin-left: 20%;
    }
    .el-dialog__body{
        width: 60%;
        margin-left: 10%;
    }
    
    
</style>