<script>
import login from "../components/Login"
export default {
  name:"headnav",
  data(){
    return{
      routerList:["index","diary","message","links","about"],//nav内容列表
      isList:false,//下拉菜单
      LoginOK:false,
      dailog:{
        show:false,
        loginDate:""
      }
    }
  },
  components:{
      login
  },
  computed:{
    num(){ //导航栏特定颜色
        let index = this.routerList.indexOf(this.$route.name);
        return index ; 
    }
  },
  methods:{
    headleClick(){  //移动端中下拉菜单的收缩/展开
      if(this.isList){
        this.isList = false;
      }else{
        this.isList = true;
      }
    },
    headleLogin(){ //登录弹框
        this.dailog.show = true;
        // console.log(this.dailog)
    },
    outLogin(){
        window.sessionStorage.removeItem('eleToken');
        this.LoginOK = false;
        this.$message({
            message:"退出成功",
            type:"success"
        })
    }

  },
  created(){
      if(window.sessionStorage.eleToken){
          this.LoginOK = true;
      }else{
          this.LoginOK = false;
      }
  },
}
</script>
