<template>
    <div class="message">
        <header-Nav></header-Nav>
        <div class="message-box">
            <div class="talk-main">
                <div class="word">
                    <h1>留言信笺</h1>
                    <h2>酒逢知己千杯少,话不投机半句多</h2>
                    
                </div>
                <div>
                    <Rtext @bui="textData"></Rtext>
                    <p>留言框不正常,刷新即可</p>
                </div>

            </div>
            <div class="talkeach">
                <ul>
                    <li v-for="(item,index) in reply" :key="index">
                        <a href=""><img src="../assets/img/avatar1.jpg" alt=""></a>
                        <div class="own">
                            <span>游客留言:</span>
                            <div v-html="item.content"></div>
                            <span>{{item.date |time}}</span>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import headerNav from '@/components/HeaderNav.vue'
import Rtext from "../components/RichText"
export default {
    name:"message",
    data(){
        return{
            allReply:"",
            reply:[
                {
                    content:"天降霜露吾黑发,取其掌中即融化",
                    date:"2020-2-2"
                },
                {
                    content:"穿行引马野， 榛叶红欲燃。吾衣染其色， 为证羁旅还",
                    date:"2020-4-23"
                }
            ]
        }
    },
    filters:{
        time(value){
            let val = new Date(value)
            return val.toLocaleString();
        }
    },
    components:{
        headerNav,
        Rtext
    },
    methods:{
        textData(value){//接受留言板提交的内容
            this.$axios.post("/api/message/reply",{value})
                .then(res=>{
                    this.$message({
                        message:"留言成功",
                        type:"success"
                    })

                })
                .catch(err=>{
                    console.log(err)
                })
        },
        getFullReply(){
            this.$axios.post("/api/message/allReply")
                .then(res=>{
                    this.allReply = res.data
                    // console.log(this.allReply)
                })
                .catch(err=>{
                    console.log(err)
                })
        }   
    },
    mounted(){
        this.getFullReply()
    }
}
</script>
<style lang="less">
html{
    font-size: 100%;
}
.message{
    width: 100%;
    overflow: hidden;
    .message-box{
        box-sizing: border-box;
        margin: 80px auto;
        width: 100%;
        max-width: 1360px;
        min-height: 800px;
        padding: 20px ;

        .talk-main{
            width: 100%;
            // height: 500px;
            background-color: #fff;
            overflow: hidden;
            .word{
                margin-top: 30px;
                width: 100%;
                text-align: center;
                font-size: 0.7rem;
                line-height: 2rem;
            }
            .rText{
                 margin: 50px 25px 0 25px;
            }
            .layui-btn{
                margin:10px;
            }
            p{
                margin:0 0 10px 20px;
                font-size: 0.5rem;
                
            }
            
        }
        .talkeach{
            width: 100%;
            // height: 500px;
            margin-top: 20px;
            padding: 20px;
            background-color: #fff;
            box-sizing: border-box;
            ul{
                width: 100%;
                li{
                    width: 100%;
                    display: flex;
                    box-sizing: border-box;
                    padding: 8px 0;
                    border-bottom: 1px dashed grey;
                    a{
                        display: block;
                        width: 40px;
                        height: 40px;
                        overflow: hidden;
                        // background-color: red;
                        border-radius: 50%;
                        img{
                            width: 100%;
                            max-height: 1005;
                        }
                    }
                    .own{
                        flex: 1;
                        margin:10px 0 0 10px;
                        span{
                            color:darkcyan;
                            font-size: 0.8rem;
                        }
                        div{
                            text-indent: 1rem;
                            margin: 5px 0;
                        }
                    }
                }
            }
        }
    }
}
@media screen and (min-width: 992px) {
    .message .message-box .talk-main .word{
        font-size: 1rem;
        line-height: 3rem;    
    }
}
</style>