<template>
    <div class="content">
        <div class="con">
            <section class="left">
                <articleShow></articleShow>
            </section>

            <section class="right">
                <div :class="['side1',{isfixed:isfixed}]">
                    <div class="search">
                        <input type="text" placeholder="关键字搜索" v-model="searchList">
                        <i class="layui-icon layui-icon-search" @click="headleSeach"></i>
                        <div class="searchList">
                            <ul>
                                <li v-for="(item,index) in conList" :key="index">
                                    <router-link :to="'/article/'+item._id">{{item.title}}</router-link>
                                    <!-- <a href="">哈哈哈</a> -->
                                </li>
                            </ul>
                        </div>   
                    </div>
                    <div class="conList">
                        <ul>
                            <li v-for="(item,index) in articleTags" :key="index" :class="'num'+index">
                                <router-link :to="'/index/'+index">{{item}}</router-link>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="side2">
                    <div class="me">
                        <img src="../assets/img/8.jpeg" alt="">
                        <span>sjhv - ENn</span>
                        <p>费思量 , 自难忘</p>
                    </div>
                    <div class="website">
                        <i class="layui-icon layui-icon-website"> 常用网站</i>  
                        <ul>
                            <li><a href="https://github.com/">GitHub</a></li>
                            <li><a href="https://coding.net/">Coding</a></li>
                            <li><a href="https://www.csdn.net/">CSDN</a></li>
                            <li><a href="https://www.jianshu.com/">简书</a></li>
                            <li><a href="https://juejin.im/">掘金</a></li>
                            <li><a href="https://www.upyun.com/">又拍云</a></li>
                        </ul>
                    </div>
                </div>
                <div class="side3">
                    <div class="share">
                        <i class="layui-icon layui-icon-share"> 源码分享</i> 
                    </div>
                        <ul>
                            <li><a href="">博客模板</a></li>
                            <li><a href="">后台管理系统</a></li>
                            <li><a href="">网页开发</a></li>
                            <li><a href="">123</a></li>
                        </ul> 
                </div>
            </section>

        </div>
    </div>
</template>

<script>
import articleShow from "../components/Article"
export default {
    name:"con",
    data(){
        return{
            articleTags:["全部文章","Html&css","JavaScript","Vue","Node.js","其他"],
            isfixed:false,
            searchList:"", //搜索关键字
            conList:"",//搜索后的内容
        }
    },
    methods:{
        //右侧导航栏下坠
        hedaleScroll(){  
            let scrollTop = document.documentElement.scrollTop
            this.isfixed = scrollTop>=1000  //判断运算符返回布尔值
        },
        headleSeach(){//搜索功能
            let keyWords = this.searchList.trim();
            // console.log(keyWords)

            if(!keyWords){
                this.$message({message:"请输入查找内容"})
            }else{
                this.$axios.post("/api/article/search",{keyWords})
                    .then(res=>{
                        this.conList = res.data
                        // console.log(res.data)
                        // this.searchList = null
                    })
                    .catch(err=>{
                        console.log(err)
                    })
            }
        }   
    },
    components:{
        articleShow, //挂载文章组件
    },
    mounted(){
            //监听window的srcoll
            window.addEventListener("scroll",this.hedaleScroll)
        },
    destroyed(){ //只监听博客页面的srcoll  其他页面不监听 就销毁这个监听事件
        window.removeEventListener("scroll",this.hedaleScroll)
    }
}
</script>

<style lang="less" >
    .html{
        font-size: 100%;
    }
    .content{
        width: 100%;
        max-width: 1360px;
        box-sizing: border-box;
        padding: 80px 20px;
    }
    .con{
        width: 100%;
        margin: 0px auto;
        display: flex;
        user-select: none;
    }
    .con .left{
        flex: 1;
        height: 100%;
    }
    .con .right{
        margin-left: 10px;
        width: 300px;
        display: none;
        > .side1{
            width: 100%;
            background-color: #fff;
            position: relative;
            > .search{
                width: 100%;
                background-color: grey;
                > input{
                    width: 80%;
                    height: 35px;
                    margin: 10px 10%;
                    border-radius: 20px;
                    outline: 0; //外线不要
                    box-shadow: none; //去除阴影
                    border: none;  //去除边框线
                    text-indent: 1rem;    
                }
                > i{
                    position: absolute;
                    right: 15%;
                    top: 16px;
                    font-weight: bold;
                }
                > .searchList{
                    width: 100%;
                    // height: 300px;
                    background-color: rgba(0,0,0,0.1);
                    overflow: hidden;
                    
                    ul{
                        width: 100%;
                        box-sizing: border-box;
                        
                        li{
                            border-bottom: 1px solid grey;
                            margin:10px 40px 8px;
                            box-sizing: border-box;
                            
                            a{
                                display: block;
                                font-size: 1rem;
                            }
                            a:hover{
                                color: white;  
                            }
                            
                        }
                    }
                    
                    
                }
            }
            > .conList{
                width: 100%;
                >ul{
                    width: 100%;
                    >li{
                        box-sizing: border-box;
                        // padding: 10px 0;
                        border-bottom: 1px solid #f8f9f7;
                        font-weight: bold;
                        text-indent: 2.5rem;
                        > a{
                            display: block;
                            line-height: 2.5rem;
                        }
                        > a:hover{
                            background-color: rgba(0,0,0,0.1);
                            color: #6BC30D;
                        }
                    }
                }
            }
        } 
        //搜索框下坠
        > .isfixed{
            position: fixed;
            width: 300px;
            z-index: 2;
            animation: Dfixed forwards 0.8s 1; //动画
            @keyframes Dfixed {
                0%{top:-500px}
                40%{top:80px} 
                60%{top:50px} //回弹
                100%{top:80px}
            }
        }  
        > .side2{
            margin-top: 20px;
            width: 100%;
            // height: 500px;
            background-color: #fff;
            overflow: hidden;
            > .me{
                width: 70%;
                height: 135px;
                background-color: pink;
                margin: 25px auto;
                > img{
                    width: 100%;
                    max-width: 100%;
                    height: 100%;
                    display: block;
                }
                >span{
                    margin:10px auto;
                    display: block;
                    font-size: 1.5rem;
                    text-align: center;
                    width: 100%;
                    line-height: 30px;
                    font-family: BarbaraHand;
                }
                > p{
                    width: 100%;
                    text-align: center;
                    font-size: 0.8rem;
                    font-weight: bold;
                }
            }
            > .website{
                width: 100%;
                padding-top: 90px;
                > i{
                    display: block;
                    width: 100%;
                    height: 30px;
                    // background-color: red;
                    text-align: center;
                }
                >ul{
                    width: 100%;
                    display: flex;
                    flex-wrap: wrap;  
                    margin-bottom: 10px;  
                    >li{
                        width: 50%;
                        justify-content: space-between;
                        text-align: center;
                        margin-bottom: 5px;
                        >a{
                            font-size: 1rem;
                            line-height: 2rem;
                        }
                        > a:hover{
                            color: #6BC30D;
                        }
                    }
                }
            }
        }
        > .side3{
            margin-top: 20px;
            width: 100%;
            background-color: #fff;
            padding-bottom: 10px;
            > .share{
                width: 100%;
                box-sizing: border-box;
                padding: 13px 40px;
                border-bottom: 1px solid lightgray;
                > i {
                    font-size: 1rem;
                }
                
            }
            > ul{
                    margin-top: 10px;
                    width: 100%;
                    padding-left: 40px;
                    >li{
                        width: 100%;
                        >a{
                            font-size: 1rem;
                            line-height: 2rem;
                        }
                        >a:hover{
                           color: #6BC30D;
                           font-weight: bold;
                        }
                    }
                }
        }
    
}

@media screen and (min-width: 992px){
    .con .right{
        display: block;
    }
}
</style>