<template>
    <div class="article " >
        <ul class="artList">
            <li v-for="(item,index) in artList" :key="index">
                <div class="interduce">
                    <h2 class="type">【{{item.type}}】</h2>
                    <h2>
                        <router-link :to="'/article/'+item._id">{{item.title}}</router-link>
                    </h2>
                </div>
                <div class="time">
                    <p>{{item.date | time}}</p> 
                </div>
                <div class="content clearfix">
                    <a href="#" class="img "><img src="../assets/img/art.jpg" alt=""></a>
                    <p>{{item.content}}</p>
                </div>
                <div class="readmore">
                    <!-- <a href=""></a> -->
                    <router-link :to="'/article/'+item._id">阅读全文</router-link>
                    <p>
                        <i class="el-icon-edit"></i>
                        {{item.tag}}
                    </p>
                </div>
               
            </li>
            
        </ul>
        <p v-if="loading" >Loading ...</p>
        <p v-if="noMore" >帖子还在来的路上 ...</p>
    </div>
</template>

<script>
export default {
    name:"articleShow",
    data(){
        return{
            article:[],//请求的文章数据
            isfresh:true,//文章是否可以从0开始滚动加载
            noMore:false,
            loading:false,
            artList:[
                {
                    type:"原创",
                    title:"时间",
                    content:"时间没有等我，是你忘了带我走，我左手过目不忘的的萤火，右手里是十年一个漫长的打坐。",
                    date:"2020-6-20",
                    tag:"Other"
                },
                {
                    type:"原创",
                    title:"归澈",
                    content:"柔和的阳光斜挂在苍松翠柏不凋的枝叶上，显得那么安静肃穆，绿色的草坪和白色的水泥道貌岸然上，脚步是那么轻起轻落，大家的心中却是那么的激动与思绪波涌。",
                    date:"2020-6-21",
                    tag:"Other"
                },
                {
                    type:"原创",
                    title:"舞舞舞",
                    content:"我们微笑着说 我们停留在时光的原处 其实早已被洪流无声地卷走",
                    date:"2020-6-22",
                    tag:"Other"
                },
                {
                    type:"原创",
                    title:"回头",
                    content:"我忘了哪年哪月的哪一日 我在哪面墙上刻下一张脸 一张微笑着 忧伤着 凝望我的脸",
                    date:"2020-3-20",
                    tag:"Other"
                },
                {
                    type:"原创",
                    title:"深夜",
                    content:"如果我能够继续等待，如果时间能够停留下来，如果没有如果？",
                    date:"2020-4-20",
                    tag:"Other"
                },
            ]
        }
    },
    filters:{
        time(value){
            let val = new Date(value) //改变数据类型, 数据库中的都是字符串类型
            return val.toLocaleDateString() ;
        }
    },
    computed:{
        id(){ //监听id的变化,文章数据随着id变化
            return this.$route.params.aid*1
        }
    },
    watch:{
        id(){ //监听路由id的变化,有变化执行
            // location.reload();  //js强制刷新页面
            this.getFile();

        }
    },
    methods:{
        automate:(function(){ //自动执行我们的闭包函数
            let skip = 0;
            let limit = 5;
            return function(num,isfresh = false){
                if(isfresh){
                    skip = 0;
                    limit = 5;
                }
                // let num = this.$route.params.aid*1
                let tag  = ["","Html&css","JavaScript","Vue","Node.js","Other"][num]
                let data = {tag,skip,limit}
                skip += limit;  // 文章返回回来的总数量  //这里注意空格 有时候这里的请求很奇怪
                return this.$axios.post("/api/article/checkSureFile",data)
            }
        })(),
        getFile(){ //文章请求
            this.automate(this.id, true)
                .then(res=>{
                    // console.log(res)
                    this.article = res.data
                })
                .catch(err=>{
                    console.log(err)
                })
                
        },
        headleScroll(){ //滚动加载
            if(this.noMore || this.loading) return; 
                let a = document.documentElement.scrollTop;  //滚动条高度
                // console.log(a)
                let b = document.documentElement.offsetHeight; //文档高度
                // console.log(b)
                let c = document.documentElement.clientHeight; //可视区的高度
                if(a+c > b -200){
                    // console.log(123)
                    this.loading = true
                    this.automate(this.id,false)
                        
                        .then(res=>{
                            
                            this.loading = false;
                            if(res.data.length){ //还有数据时

                                this.article.push(...res.data);

                            }else{ //无数据时
                                // this.loading = false;
                                this.noMore = true;
                            }
                        })
                        .catch(err=>{
                            console.log("大舅子,出BUG了")
                        })
                }
                
            
        }
    },
    mounted(){
        this.getFile(); //页面加载的时候自动执行
        window.addEventListener("scroll",this.headleScroll)
    },
    destroyed(){ //销毁sceoll这个监听事件
        window.removeEventListener("scroll",this.headleScroll)
    }
}
</script>

<style lang="less">
    html{
        font-size: 100%;
    }
    .clearfix::after{
        content: "";
        display: block;
        clear: both;
    }
    .article{
        width: 100%;
        > .artList{
            width: 100%;
            >li{
                box-sizing: border-box;
                width: 100%;
                background-color: #fff;
                position: relative;
                padding: 20px;
                margin-bottom: 20px;
                .interduce{
                    width: 100%;
                    h2{
                        display: inline;
                        
                    }
                    .type{
                        color: deepskyblue;
                    }
                }
                .time{
                    position: absolute;
                    top: 20px;
                    right: 40px;
                    text-align: center;
                    p{
                        font-size: 1.2rem;
                        color: #6BC30D;
                        
                    }
                }
                .content{
                    width: 100%;
                    // background-color: skyblue;
                    padding: 10px;
                    .img{
                        display: block;
                        width: 300px;
                        height: 15%;
                        // border: 1px solid #e8e9e7;
                        overflow: hidden;
                        float: left;
                        img{
                            display: block;
                            width: 100%!important;
                            // height: 100%!important;
                        }
                    }
                    p{
                        //文本折叠
                        display: -webkit-box;
                        -webkit-box-orient: vertical;
                        -webkit-line-clamp: 2;
                        overflow: hidden;
                        text-indent: 1rem;
                        padding: 0 5px;
                    }
                }
                .readmore{
                    width: 100%;
                    // background-color: red;
                    position: relative;
                    overflow: hidden;
                    
                    a{
                        // display: block;
                        font-size: 1rem;
                        margin-left: 1rem; 
                        padding: 0 5px;
                    }
                    a:hover{
                        background-color: #6BC30D;
                        color: #fff;
                        
                    }
                    p{
                        margin:5px 0 0 15px;
                        font-size: 0.5rem;
                        font-weight: bold;
                        i{font-weight: bold;}
                    }
                    
                }
                .readmore::after{
                    content: "";
                    display: block;
                    width: 100%;
                    height: 1px;
                    background-color: #d0d0d0;
                    position: absolute;
                    top: 12px;
                    right: 0;
                    left: 100px;
                   
                }
            }
        }
        > p{
            width: 100%;
            text-align: center;
            font-size: 1.5rem;
            color: #fff;
        }
        
    }

@media screen and (max-width: 768px){
    
    .time{
        display: none;
    }
    .article .artList li .content p{
        padding-top: 10px;
    }
}
@media screen and (max-width: 500px){
    .article .artList li .content .img{
        width: 100%;
    }
}
</style>